//import Carbon from '../../images/carbon.jpg';
import BlueHwScape from '../../images/hwscape.png';

const FirstLeftText = () => <p>Overview</p>;

const FirstRightText = () => (
    <p>
      Persistent Memory is redefining how we think about storage systems.  It
      allows us to keep durable data close to the CPU allowing efficient
      in-place operations with minimal data movement. 
    </p>
);


//      avoid read/write amplification
//      that arises in traditional block-centric storage systems.

//      Coupled with high-performance networking and zero-copy data movement, Persistent Memory
